@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap);
:root {
  --background: #231D2B;
  --counter-up: #2C2C44;
  --counter-down: #32374D;
  --counter-value: hsl(345, 95%, 68%);
  --counter-type: hsl(237, 18%, 59%);

  --turn-easing: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  --turn-duration: 0.6s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Red Hat Text', sans-serif;
  font-weight: 700;
}
.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding: 20px;
  background-color: var(--background);
  overflow: hidden;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  z-index: 1000;
}

.main__text {
  margin-bottom: 40px;
  color: white;
  text-align: center;
  font-size: 20px;
  letter-spacing: 6px;
  text-transform: uppercase;
}

@media only screen and (min-width: 420px) {
  .main {
    margin-top: 100px;
  }
}
.background svg:first-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}

.background svg:last-child {
  position: fixed;
  right: 0;
  bottom: 0;
  height: 30vh;
  z-index: 10;
}


@media only screen and (min-width: 1250px) {
  .background svg:last-child {
    width: 100vw;
    height: auto;
  }
}
.timer {
  position: relative;
  display: flex;
  z-index: 1000;
}

.timer--hidden {
  position: absolute;
  opacity: 0;
}
.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60px;
  margin-right: 10px;
}

.counter:last-child {
  margin-right: 0;
}

.counter__card {
  position: relative;
  -webkit-perspective-origin: 50% 50%;
          perspective-origin: 50% 50%;
  -webkit-perspective: 300px;
          perspective: 300px;
  width: 100%;
  height: 60px;
  margin-bottom: 10px;
  border-radius: 12px;
  background-color: var(--counter-down);
  text-align: center;
}

.counter__circles {
  position: absolute;
  top: 50%;
  left: -1%;
  width: 102%;
  display: flex;
  justify-content: space-between;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 0;
  overflow: hidden;
}

.circle {
  width: 0.8em;
  height: 0.8em;
  border-radius: 100%;
  background-color: #171924;
}

.circle--left {
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.circle--right {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.counter__value {
  color: var(--counter-value);
}

.counter__type {
  font-size: 7px;
  letter-spacing: 4px;
  color: var(--counter-type);
  text-align: center;
  text-transform: uppercase;
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

/* Flip Animation */

.upperCard,
.lowerCard {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  height: 50%;
  overflow: hidden;
}

.upperCard {
  align-items: flex-end;
  background-color: var(--counter-up);
  border-bottom: 0.25px solid transparent;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.lowerCard {
  align-items: flex-start;
  border-top: 0.25px solid transparent;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.upperCard span,
.lowerCard span,
.flipCard span {
  font-size: 24px;
  color: var(--counter-value);
}

.upperCard span {
  -webkit-transform: translateY(50%);
          transform: translateY(50%)
}

.lowerCard span {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%)
}

.flipCard {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  width: 100%;
  height: 50%;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.flipCard.unfold {
  top: 50%;
  align-items: flex-start;
  -webkit-transform-origin: 50% 0%;
          transform-origin: 50% 0%;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  background-color: var(--counter-down);
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  border: 0.25px solid transparent;
  border-top: 0.25px solid transparent;
}

.flipCard.unfold span {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.flipCard.fold {
  top: 0%;
  align-items: flex-end;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
  background-color: var(--counter-up);
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border: 0.25px solid transparent;
  border-bottom: 0.25px solid transparent;
}

.flipCard.fold span {
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
}

.fold {
  -webkit-animation: fold var(--turn-duration) var(--turn-easing) 0s 1 normal forwards;
          animation: fold var(--turn-duration) var(--turn-easing) 0s 1 normal forwards;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.unfold {
  -webkit-animation: unfold var(--turn-duration) var(--turn-easing) 0s 1 normal forwards;
          animation: unfold var(--turn-duration) var(--turn-easing) 0s 1 normal forwards;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

@-webkit-keyframes fold {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  100% {
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg)
  }
}

@keyframes fold {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  100% {
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg)
  }
}

@-webkit-keyframes unfold {
  from {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg)
  }

  to {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg)
  }
}

@keyframes unfold {
  from {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg)
  }

  to {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg)
  }
}

@media only screen and (min-width: 380px) {
  .counter {
    width: 70px;
    margin-right: 16px;
  }

  .counter__card {
    height: 70px;
  }

  .upperCard span,
  .lowerCard span,
  .flipCard span {
    font-size: 26px;
  }
}

@media only screen and (min-width: 600px) {
  .counter {
    width: 80px;
    margin-right: 18px;
  }

  .counter__card {
    height: 80px;
  }

  .upperCard span,
  .lowerCard span,
  .flipCard span {
    font-size: 28px;
  }

  .counter__type {
    font-size: 8px;
  }
}

@media only screen and (min-width: 800px) {
  .counter {
    width: 100px;
    margin-right: 20px;
  }

  .counter__card {
    height: 100px;
  }

  .upperCard span,
  .lowerCard span,
  .flipCard span {
    font-size: 40px;
  }

  .counter__type {
    font-size: 10px;
  }
}
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  align-self: center;
}

.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}

@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}
footer {
  position: absolute;
  bottom: 6%;
  left: 50%;
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1000;
}

footer a:hover * {
  fill: white
}
